#container {
  width: 400px;
  background-color: aliceblue;
  padding: 20px;
}

input {
  width: 100%;
  padding: 5px;
  text-transform: capitalize;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

ul,li{
    margin: 0;
    padding: 0;
    list-style: none;
}

.list{
    margin: 15px 0;
}

.list > li {
    background-color: antiquewhite;
    padding: 5px;
    margin-bottom: 2px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.list > li > span:first-child{
    text-transform: capitalize;
}

.list > li > a{
    text-decoration: none;
    color: white;
    background-color: red;
    padding: .2rem .5rem;
}

.btn{
    display: flex;
    justify-content: right;
    margin-top: 1rem;
    padding: 5px 0 ;
}

.btn > button{
    padding: 10px 1.5rem;
}